.transaction_history{
    width: 600px;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 0 6px 0 #adacac;
}

.transaction_history th{
    background-color: #00bcd5;
    color: #fff;
    padding: 12px;
    text-transform: uppercase;
}

.transaction_history tr{
    color:#787d81;
}

.transaction_history tr:nth-child(odd){
    background-color: #ecf1f3;
}

.transaction_history td{
    padding: 12px;
    text-align: center;
}
